@import "../../styles/variables.sass";

$button-margin: 0.25em;
$non-interactive-color: #888;

.Item {
  border-radius: 1em;

  img {
    width: 3em; }

  button {
    margin: 1em $button-margin $button-margin; }

  svg {
    margin-bottom: -0.25em; }

  .MuiCardContent-root {
    padding-top: 0;

    p {
      padding-top: 1em;

      &.detail {
        color: $non-interactive-color; } } }

  .MuiCardActions-root {
    flex-wrap: wrap;
    padding-top: 0; }

  .quantity {
    align-items: flex-end;
    display: flex;

    button {
      cursor: pointer;
      font-size: 16px;
      font-weight: normal;
      margin-left: 0; } }

  .MuiInput-root {
    margin-right: 0.25em;
    max-width: 75px;

    input {
      margin-top: 1em;
      text-align: right; } } }
