$not-allowed-color: rgba(255, 0, 0, 0.25);

.Plot {
  background-repeat: no-repeat;
  background-size: cover;
  border: solid 1px #000;
  flex-grow: 1;
  image-rendering: pixelated;

  .plant-mode &:hover {
    background-color: #d7d7d7;
    cursor: pointer; }

  .water-mode &.crop:hover {
    background-color: #7df5ff;
    cursor: pointer; }

  .fertilize-mode &.crop:not(.is-fertilized) {
    background-color: rgba(125, 56, 0, .75);

    &:hover {
      cursor: pointer; } }

  .harvest-mode &.is-ripe {
    background-color: rgba(0, 255, 0, 0.25);

    &:hover {
      cursor: pointer; } }

  .cleanup-mode &.crop {
    background-color: rgba(255, 255, 0, 0.25);
    cursor: pointer; }

  .cleanup-mode &.is-replantable {
    background-color: rgba(0, 255, 0, 0.25);
    cursor: pointer; }

  .cleanup-mode &.is-ripe {
    background-color: $not-allowed-color;
    cursor: auto; }

  .set-sprinkler-mode:hover &.is-in-hover-range {
    background-color: #ccc; }

  .set-sprinkler-mode:hover &:hover,
  .set-scarecrow-mode:hover &:hover {

    &.is-empty img {
      cursor: pointer;
      opacity: 0.5; }

    &:not(.is-empty) {
      background-color: $not-allowed-color;
      background-image: none;
      cursor: not-allowed; } }

  .set-sprinkler-mode:hover &:hover {

    &.is-empty img {
      background-image: url(../../img/items/sprinkler.png); } }

  .set-scarecrow-mode:hover &:hover {

    &.is-empty img {
      background-image: url(../../img/items/scarecrow.png); } } }
